import { getCardSalesInfoById,getCompanyInfoById,addActiverecord } from '@/services/services.js'

// 
const previewImg = function(arg,index=0){
	let arr = arg

	if(Object.prototype.toString.call(arg) !== '[object Array]'){
		arr = [arg]
	}
	
	// 
	uni.previewImage({
		urls:arr,
		current: index,
		longPressActions: {
			itemList: ['', '', ''],
			success: function(data) {
				console.log('' + (data.tapIndex + 1) + ',' + (data.index + 1) + '');
			},
			fail: function(err) {
				console.log(err.errMsg);
			}
		},
		success:(res)=>{
			console.log('previewImag success')
			console.log(res)
		},
		fail:(res)=>{
			console.log('previewImag fail')
			console.log(res)
		}
	})
}



//   
const initGetAlbum = function(){
	return new Promise(function(resolve,rejcet){
		uni.getSetting({
			success(res){
				console.log(res)
				if(res.authSetting["scope.writePhotosAlbum"]){
					resolve(true)
				}else{
					resolve(false)
				}
			},
			fail(res){
				resolve(false)
			}
		})
	})
}

// 
const getImgInfo = function(src){
	return new Promise(function(resolve,reject){
		uni.getImageInfo({
			src: src,	
			success:(res)=>{
				console.log(res)
				resolve(res)
			},
			fail:(res)=>{
				console.log(res)
				resolve(res)
			}
		})
	})
}

